.navbar-local {
  background-color: var(--color-white);
  height: 7rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: end;
  align-items: center;
  box-shadow: 0 5px 2rem rgba(0, 0, 0, 0.2);
  margin-left: 8rem;
}

.page-title {
  display: flex;
  align-items: center;
}

.navbar-icon {
  color: var(--color-primary);
  margin-right: 7px;
  font-size: 2.4rem;
}

.user-profile-photo {
  margin-right: 4vw;
}

.dropdown-items-text {
  font-size: 1.4rem !important;
}

.dropdown-menu {
  padding: 1rem 2rem !important;
}
.dropdown-toggle::after {
  display: none !important;
}

.menu-bars {
  margin-left: 1.2rem;
  font-size: 4rem;
  background: none;
  color: var(--color-white);
  text-decoration: none;
  display: flex;
  align-items: center;
}

.menu-bars span {
  font-size: 1.9rem;
  font-weight: bold;
}

.menu-bars:hover {
  color: var(--color-white);
}

.nav-menu {
  background-image: linear-gradient(
    145deg,
    var(--color-primary) 30%,
    var(--color-primary-dark)
  );

  display: flex;
  justify-content: center;
  position: fixed;
  z-index: 999;

  left: -100%;
  transition: width 200ms ease;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-menu-items {
  display: flex;
  flex-direction: column;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 16px;
  list-style: none;
  height: 6rem;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 2.5rem;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 0 0 7px;
  border-radius: 4px;
  transition: all 0.1s;
}

.nav-text a:hover {
  background-color: var(--color-white);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  color: var(--color-primary-dark);
}

.link-text {
  display: none;
  margin-left: 1.6rem;
  font-size: 1.6rem;
}

.link-icon {
  min-width: 3rem;
}

.nav-menu-items {
  width: 100%;
}

.nav-toggle {
  width: 90%;
  margin: 0 auto;
  height: 7rem;
  display: flex;
  justify-content: start;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

@media (max-width: 600px) {
  .nav-menu {
    bottom: 0;
    width: 100vw;
    height: 6rem;
  }

  .menu-bars {
    display: none;
  }

  .nav-menu-items {
    flex-direction: row;
    justify-content: center;
    align-items: baseline;
  }

  .nav-toggle {
    display: none;
  }

  .settings {
    display: none;
  }

  .settings-container {
    display: flex;
  }

  .nav-text a {
    padding: 0 5px;
  }

  .navbar-local {
    margin-left: 0;
  }
  .nav-text {
    padding: 1rem;
  }
}

@media (max-width: 490px) {
  .nav-text {
    padding: 1rem 2px;
  }
}

@media (min-width: 600px) {
  .nav-menu {
    top: 0;
    width: 8rem;
    height: 100vh;
  }

  .nav-menu:hover {
    width: 23rem;
  }

  .nav-menu:hover .link-text {
    display: inline;
  }
}

.settings {
  width: 90%;
  margin: 2rem auto;
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.sup-chat {
  margin: auto 0 5px 0;
}

.sup-chat a {
  background-color: rgba(233, 154, 85, 0.363);
  border-radius: 4px;
}

.profile-photo {
  background-color: var(--color-white) !important;
  border: none !important;
  padding-top: 1rem !important;
  outline: 0 !important;
}
